import uniGoodsNav from '@/components/uni-goods-nav/uni-goods-nav.vue'
import uniPopup from "@/components/uni-popup/uni-popup.vue"
import uniNumberBox from "@/components/uni-number-box/uni-number-box.vue"
import {
	AddShiping,
	GetGoodDetail,
	GetDefaultAdd
} from "../../common/api.js"
export default {
	data() {
		return {
			detData: {},
			car: {
				product_id: "",
				number: 1
			},
			options: [{
				icon: '../../static/img/index.png',
				text: '首页'
			}],
			buttonGroup: [{
					text: '加入购物车',
					backgroundColor: '#ffa200',
					color: '#fff'
				},
				{
					text: '立即购买',
					backgroundColor: '#ff0000',
					color: '#fff'
				}
			]
		}
	},
	components: {
		uniGoodsNav,
		uniPopup,
		uniNumberBox
	},
	onLoad(option) {
		this.car.product_id = option.gid;
		this.pageInit(option.gid)
	},
	methods: {
		pageInit(pid) {
			console.log("页面初始化请求")
			let d = {
				product_id: pid
			};
			GetGoodDetail(d).then(res => {
				if (res.status) {
					this.detData = res.data;
				}
			})
		},
		// bindChange(val){
		// 	this.car.number = val;
		// },
		onClick(e) {
			uni.switchTab({
				url: '../index/index'
			});
		},
		buttonClick() {
			this.$refs.popup.open();
		},
		close() {
			// console.log("点击了关闭")
			this.$refs.popup.close();
		},
		// tempNag(data){},
		payGood(){
			GetDefaultAdd().then(res=>{
				if(res.status){
					let d = [];
					d[0] = this.detData;
					d[0].number = this.car.number;
					let tp = (((this.detData.price)*(this.car.number)))/100
					this.$store.commit("SET_SETARR", d);
					this.$store.commit("SET_DEFAADD",res.data);
					this.$store.commit("SET_PRICE",tp);
					uni.navigateTo({
					    url: '../settlement/settlement?pay_type=0'
					});
				}else{
					if(res.code == '204'){
						let d = [];
						d[0]= this.detData;
						d[0].number = this.car.number;
						let tp = (((this.detData.price)*(this.car.number)))/100
						this.$store.commit("SET_SETARR", d);
						this.$store.commit("SET_DEFAADD",null);
						this.$store.commit("SET_PRICE",tp);
						uni.navigateTo({
						    url: '../settlement/settlement?pay_type=0'
						});
					}
				}
			}) 
		},
		addShiping() {
			AddShiping(this.car).then(res => {
				if (res.status) {
					uni.switchTab({
						url: '../shopcar/shopcar',
						success: function() {
							let page = getCurrentPages().pop();
							if (page == undefined || page == null) return;
							page.onLoad();
						}
					});
				} else {
					if(res.code=='201'){
						uni.showToast({
							icon: "none",
							title: '添加失败，该商品已在购物车'
						});
					}else{
						uni.showToast({
							icon: "none",
							title: '添加失败，请稍后重试'
						});
					}
					
				}
			})
		}
	}
}
